/* src/App.css â€” ONLY Recommendation polish added. Everything else preserved. */

:root{
  --bg:#f6fbfc;
  --card:#ffffff;
  --muted:#6b7b86;
  --accent-start:#2bd0c8;
  --accent-end:#1f6ef8;
  --pill:#e9f6ff;
  --focus:#2ea8ff;
}

/* base */
*{box-sizing:border-box;font-family: 'Inter', 'Helvetica Neue', Arial, sans-serif}
body,html,#root{height:100%;margin:0;background:var(--bg);color:#0b1a23}
.app{max-width:900px;margin:0 auto;padding:14px 12px 80px}

/* header */
.app-header{
  display:flex;align-items:center;justify-content:space-between;gap:12px;flex-wrap:nowrap;margin-bottom:6px
}
.title{
  font-size:20px;margin:0;font-weight:700;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;max-width:62%
}
.tab-buttons{
  display:flex;gap:8px;align-items:center;flex:1 1 auto;justify-content:flex-end;overflow-x:auto;-webkit-overflow-scrolling:touch
}
.tab{
  padding:6px 10px;border-radius:12px;border:none;background:transparent;white-space:nowrap;font-size:14px;cursor:pointer
}
.tab.active{
  background:linear-gradient(90deg,var(--accent-start),var(--accent-end));
  color:#fff;box-shadow:0 6px 18px rgba(31,110,248,0.12)
}

/* top row */
.top-row{display:flex;align-items:flex-start;gap:10px;flex-wrap:wrap}
.avg-card{padding:10px;border-radius:10px;background:var(--card);box-shadow:0 8px 20px rgba(12,28,58,0.06);min-width:160px}
.avg-label{color:var(--muted);font-size:12px}
.avg-number{font-size:20px;font-weight:800}

/* filter */
.filter-row{display:flex;align-items:center;gap:8px;flex:0 1 auto;justify-content:flex-end;min-width:0}
.filter-left{display:flex;align-items:center;gap:8px;min-width:0}
.filter-label{color:var(--muted);font-size:13px;white-space:nowrap}
.filter-select{padding:6px 8px;border-radius:8px;border:1px solid #e6eef5;background:#fff;font-size:13px}
.filter-right{display:flex;align-items:center;gap:8px}
.reset-ui-btn{
  padding:6px 10px;border-radius:10px;border:none;background:#fff;color:#1b6ea8;
  cursor:pointer;box-shadow:0 6px 12px rgba(13,26,40,0.04);white-space:nowrap;font-size:13px
}

/* controls */
.controls{margin-top:10px;display:flex;flex-direction:column;gap:8px}
.control-head{display:flex;align-items:center;gap:8px}
.info-btn{
  border-radius:50%;width:26px;height:26px;background:#eef6fb;border:none;
  cursor:pointer;display:inline-flex;align-items:center;justify-content:center
}
.legend{font-size:12px;color:var(--muted)}

/* =========================
   Recommendation (POLISHED)
   ========================= */
.recommendation-row{
  display:flex;align-items:center;margin:10px 0 6px
}

/* pill becomes visual card */
.rec-pill{
  display:grid;
  grid-template-columns:auto 1fr auto;
  align-items:center;
  gap:10px;
  padding:10px 14px;
  background:linear-gradient(180deg,#ffffff,#f7fbff);
  border-radius:14px;
  box-shadow:0 10px 24px rgba(12,28,58,0.08);
}

/* emoji slightly larger, acts as visual anchor */
.rec-emoji{
  font-size:26px;
  line-height:1;
}

/* hand name: primary text */
.rec-text{
  font-size:15px;
  font-weight:700;
  letter-spacing:0.02em;
}

/* score: secondary, de-emphasized */
.rec-score{
  font-size:12px;
  color:var(--muted);
  white-space:nowrap;
}

/* ========================= */

/* hand cards */
.hand-cards{display:flex;gap:10px;margin-top:12px;overflow-x:auto;padding:8px 6px 6px}
.hand-card{
  flex:0 0 110px;padding:14px 10px;border-radius:12px;
  background:linear-gradient(180deg,#fff,#fbfdff);
  text-align:center;box-shadow:0 10px 30px rgba(12,28,58,0.06);
  cursor:pointer;border:2px solid transparent;white-space:nowrap
}
.hand-card.selected{border-color:var(--focus)}
.hand-emoji{font-size:28px}
.hand-text{margin-top:6px;font-weight:700}
.hand-sub{margin-top:6px;color:var(--muted);font-size:12px;display:flex;flex-direction:column;gap:4px}

/* result */
.result-row{display:flex;gap:8px;margin-top:10px;flex-wrap:nowrap}
.pill{padding:8px 12px;border-radius:16px;border:none;background:var(--pill);cursor:pointer}
.pill.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:white}

/* actions */
.actions-row{display:flex;gap:8px;align-items:center;margin-top:10px}
.save-btn{
  padding:10px 14px;border-radius:14px;border:none;
  background:linear-gradient(90deg,var(--accent-start),var(--accent-end));
  color:white;font-weight:700;cursor:pointer
}
.back-btn.small{padding:8px 10px;border-radius:10px;background:#f3f7fa;border:none;cursor:pointer}

/* history */
.history-section{margin-top:16px}
.history-table{
  width:100%;border-collapse:collapse;background:var(--card);
  border-radius:8px;overflow:hidden;box-shadow:0 10px 30px rgba(12,28,58,0.06)
}
.history-table th,.history-table td{
  padding:10px 12px;border-bottom:1px solid #f3f7fb;white-space:nowrap;font-size:13px
}
.td-hand{text-align:center;font-size:16px}
.small-btn{
  padding:6px 10px;border-radius:8px;border:none;background:#eef6fb;
  color:#1b6ea8;cursor:pointer
}

/* stats */
.stats-section{margin-top:18px}
.chart-wrap{position:relative;height:260px;background:transparent;overflow-x:auto;overflow-y:hidden;padding-bottom:12px}
.chart-inner{min-width:640px;height:260px}

/* mobile tweaks */
@media (max-width:520px){
  .title{font-size:16px;max-width:56%}
  .tab{padding:6px 8px;font-size:13px}
  .filter-select{min-width:70px;max-width:36vw}
  .reset-ui-btn{padding:6px 8px;font-size:12px}
  .hand-card{flex:0 0 86px;padding:10px}
}
